const Tab =({tab, setTab , tabName ,index})=>{
    return (
        <div 
        className={`tab cursor-pointer px-3 py-2 rounded-2 mx-1 font-bold ${tab === index ? ' active btn-info' : 'btn-secondary'}`}
        
        style={{
          "border":  tab === index ? "1px black solid" : "1px solid #d1d1d1",
          "background": tab === index ? '#c7edf9' :'white',
          "boxShadow": '4px 4px rgba(0,0,0,0.9)' 
        }}
        
        onClick={()=>{
          setTab(index)
        }}>{tabName}</div>
    )
}

export default Tab;