import React, { Component } from 'react';
import {useState, useEffect , useRef ,useMemo ,useCallback} from "react";
import Slider from "@mui/material/Slider";
import 'bootstrap/dist/css/bootstrap.min.css';
import { GoogleMap, useJsApiLoader, Marker , Autocomplete , MarkerF, DirectionsService, DirectionsRenderer , InfoWindow} from '@react-google-maps/api';
import { Wheel } from 'react-custom-roulette'

import './styles.scss'; // Sass

import Tab from "./_parts/Tab";
import SearchItem from "./_parts/SearchItem";
import FavouriteItem from "./_parts/FavouriteItem";
import  { isEmpty, removeDuplicates, getPhoto, getDistance}  from "./methods";
import { useAppState } from "./useAppState"; // 

const libraries = ["places"];
function MyComponent() {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
    libraries:libraries,
    language:"zh-TW"
  })
  const {
    currentPosition,
    setCurrentPosition,
    tempList,
    setTempList,
    localList,
    setLocalList,
    sliderValue,
    setSliderValue,
    selectedRestaurant,
    setSelectedRestaurant,
    tab,
    setTab,
    inputValue,
    setInputValue,
    prizeNumber,
    setPrizeNumber,
    wheelShowText,
    setWheelShowText,
    autocompleteRef,
    mapElementRef,
    mustSpin, 
    setMustSpin,
    autocomplete,
    setAutocomplete,
    directions,
    setDirections
  } = useAppState();

  const  onPlaceChanged = () => {
    const place = autocomplete?.getPlace();
    getPhoto(place?.place_id).then((imgUrl) =>{
        if (!isEmpty(place)) {
          let selectedRestaurant = {
              location: place?.geometry?.location,
              placeId:place?.place_id,
              name:place?.name,
              img:imgUrl,
          }
          //parse
          const copySelectedRestaurant = JSON.parse(JSON.stringify(selectedRestaurant));
          setSelectedRestaurant(copySelectedRestaurant)
        }
      })
      setInputValue('');
  };

  const onLoad = (autocomplete) => {
    setAutocomplete(autocomplete);
    autocompleteRef.current = autocomplete;
  };

  useEffect(() => {
    navigator.geolocation.getCurrentPosition((position) => {
        const { latitude, longitude } = position.coords;
        setCurrentPosition({ lat: latitude, lng: longitude });
      },
      (error) => {
        console.log(error);
      }
    );
    setLocalList(JSON.parse(localStorage.getItem('myData')))
  }, []);

  useEffect(() => {
    
        //  selectedRestaurant.directions
        if (!isEmpty(directions) && isEmpty(selectedRestaurant.directions)) {
          const distanceText = directions.routes[0].legs[0].distance.text;
          const updatedSelectedRestaurant = {
            ...selectedRestaurant,
            directions: distanceText,
          };
            setSelectedRestaurant(updatedSelectedRestaurant);

            setTempList(prevTest =>{ 
              return  removeDuplicates([{...updatedSelectedRestaurant},...prevTest])
            });
        }
  }, [selectedRestaurant,directions]);

  const bounds = useMemo(()=>{
    let delta = getDistance(2 ,currentPosition)
    return{ 
      east: currentPosition.lng + delta.lng,
      west: currentPosition.lng - delta.lng,
      south: currentPosition.lat - delta.lat,
      north: currentPosition.lat + delta.lat,
    }
   },[currentPosition])

  const directionsServiceOptions = useMemo(() => {
    return {
      origin: currentPosition,
      destination: {
        lat: selectedRestaurant?.location?.lat,
        lng: selectedRestaurant?.location?.lng,
      },
      travelMode: 'WALKING',
    };
  }, [currentPosition, selectedRestaurant]);
  
  const autocompleteOptions = {
    fields: ["address_components", "geometry", "name",'place_id'],
    types: ["restaurant"],
    componentRestrictions: { country: "tw" },
    location: currentPosition,
    strictBounds: true,
    'bounds':bounds
  };


  //
  let filterLocalList = useMemo(()=>{

    if(isEmpty(localList)){
      return  
    }

    const newData = localList?.map((node)=>{

      if(isEmpty(node?.directions)){
        return  
      }
        return {...node , directions :Number(node?.directions.split(' ')[0])}
    })
    return newData?.filter((node)=>{ return node.directions <= sliderValue })
  },[sliderValue,localList,currentPosition])

  //
  let wheelData = useMemo(()=>{
    return filterLocalList?.reduce((acc,node)=>{
      let shortName = node.name
     if(node.name.length>5){
      shortName = node.name.slice(0, 5)+'...';
     }
      return [
            ...acc,
            {'option':shortName}
      ]
    },[])
  },[filterLocalList,localList])
 

  const directionsCallback = useCallback((response) => {
    if(!isEmpty(response)){
      setDirections(response);
      
    }
}, [setDirections]);

  const handleSpinClick = () => {
    const newPrizeNumber = Math.floor(Math.random() * wheelData?.length);
    setPrizeNumber(newPrizeNumber);
    setTimeout(()=>{
      setMustSpin(true);
    }) 
    setWheelShowText(false)
  };

  return (
  
    isLoaded  &&
    <div className="overflow-hidden">
      <GoogleMap
        id="map"
        ref={mapElementRef}
        className="relative"
        mapContainerStyle={{
          width: '100%',
          height: '100vh'
        }}
        center={currentPosition}
        zoom={16}>   
      <MarkerF position={currentPosition}></MarkerF>
      { 
       (!isEmpty(selectedRestaurant)) &&
        <>
          <MarkerF position={selectedRestaurant?.location}></MarkerF>
          <DirectionsService
            options={directionsServiceOptions}
            callback={directionsCallback}
          />
          <DirectionsRenderer
          options={{
            directions: directions,
          }}/>
          <InfoWindow position={selectedRestaurant?.location}
          options={{
            pixelOffset: { width: 0, height: -30 }, 
            zIndex: 100, 
          }}>
          <a href={`https://www.google.com/maps/dir/?api=1&destination=${selectedRestaurant?.location?.lat},${selectedRestaurant?.location?.lng}&dir_action=walk`}
          className="location text-[14px]"
          style={{
            "textDecoration": "none",
          }}>
            <div className="text-center text-black ">
              <div className="my-2 text-[16px]">{selectedRestaurant.directions}</div>
              <div className="flex flex-col">
                <img src={selectedRestaurant?.img} 
                style={{
                  width: '100%',
                  height: '120px'
                }}/>
              
                <div className="font-bold p-2 text-[18px]" style={{
                  "backgroundColor":"#f8dbdb",
                }}> {selectedRestaurant?.name} </div>
              </div>
            </div>
          </a>
          </InfoWindow>
        </>
        }
      <Autocomplete
      onLoad={onLoad} 
      restrictions={{
        country: 'tw',
      }}
      options={autocompleteOptions}
      onPlaceChanged={onPlaceChanged}>
        <div style={{
            zIndex: '1',
            position: 'absolute',
            transform: 'translate(-50%, -50%)',
            top:'4%',
            left: '50%',
            width: '80vw',
        }}>
            <input
              type="text"
              className="h-10 w-100 searchInput p-3"
              placeholder=""
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              style={{
                  "border": "2px #181818 solid",
                  "background":'white',
                  "fontWeight": 'bold'
              }}
            />
        </div>
      </Autocomplete>
    </GoogleMap>
      <div className="absolute w-100 fixed bottom-0 overflow-hidden">
          <div className="rounded-t-lg shadow-black">
            <div className="flex -mb-2">
              <Tab
              tab={tab}
              setTab={setTab}
              setSelectedRestaurant={setSelectedRestaurant}
              tabName={''}
              index ={1}
              />
              <Tab
              tab={tab}
              setTab={setTab}
              setSelectedRestaurant={setSelectedRestaurant}
              tabName={''}
              index={2}
              />
            </div>
              <div className="bg-white">
                <div className="container-lg"
                style={{
                  'maxHeight' : '40vh',
                  "borderTop":  "1.2px black solid" ,
                }}>
                {{          
                  1: <div className="overflow-scroll px-4 mt-4"
                  style={{
                    height: '40vh'
                  }}>
                    <div className="mb-4"></div>
                    <div className="p-2 h-100 w-100">
                      <div className="row p-2">
                        {
                          !isEmpty(tempList) ? 
                            tempList?.map((node,index)=>{
                                return (
                                <SearchItem
                                key={node.placeId}
                                className="cursor-pointer"
                                node={node}
                                selectedRestaurant={selectedRestaurant}
                                setSelectedRestaurant={setSelectedRestaurant}
                                localList={localList}
                                setLocalList={setLocalList}
                                />)
                              })
                              :<div className="py-10 text-center"></div>
                        }
                        </div>
                      </div>
                    </div>,
                    2: <>
                        <div className="row pt-4">
                          <div className="col-auto text-[18px]"></div>
                          <div className="col-auto text-[18px]">{ sliderValue >=1 ? `${sliderValue}KM`:`${sliderValue *1000}M`}</div>
                        </div>
                        <Slider
                          aria-label="Temperature"
                          onChange={(e)=>{
                            setSliderValue(e.target.value)
                          }}
                          value={sliderValue}
                          disabled={false}
                          marks={false}
                          max={2}
                          step={0.2}
                          min={0}
                          valueLabelDisplay="auto"
                          defaultValue={20}/>
                        <button className={`btn w-100 p-3 my-2 font-bold text-white mb-4 font-bold ${isEmpty(filterLocalList) ? 'disabled' :'' }`}
                        style={{
                          "border": "1px #181818 solid",
                          "background":'#181818',
                          "fontWeight": 'bold'
                        }}
                        onClick={()=>{
                          setTab(3)
                          handleSpinClick()
                        }}></button>

                        <div className="overflow-scroll p-2 h-44">
                            <div className="row px-4">
                              {
                                !isEmpty(filterLocalList)?
                                filterLocalList?.map((node,index)=>{
                                  return(
                                      <FavouriteItem 
                                      key={index}
                                      node={node}
                                      selectedRestaurant={selectedRestaurant}
                                      setSelectedRestaurant= {setSelectedRestaurant}
                                      filterLocalList={filterLocalList}
                                      setLocalList={setLocalList}
                                  />)
                                })
                                : <div className="text-center"></div>
                              }
                          </div>
                        </div>
                      </>,

                  3:<div className="fixed w-100 h-100 left-0 top-0 z-[99] flex"
                  style={{
                    "justifyContent": 'center',
                    "alignItems": 'flex-center',
                    "backgroundColor": '#000c',
                  }}>
                    <div className="relative"
                    style={{
                    "marginTop": 'auto',
                    "marginBottom": 'auto',
                    }}>
                    { <>
                        <Wheel
                        spinDuration={0.1}
                        mustStartSpinning={mustSpin}
                        prizeNumber={prizeNumber}
                        backgroundColors={wheelData?.length %2 === 0 ?  ['#c7edf9' ,'#faf2c7']: ['#c7edf9', '#e8dbf8' ,'#faf2c7']}
                        textColors={['#181818']}
                        innerRadius={4}
                        innerBorderColor='black'
                        innerBorderWidth={10}
                        radiusLineWidth={0}
                        outerBorderColor='white'
                        outerBorderWidth={10}
                        fontSize={18}
                        data={wheelData}
                        onStopSpinning={(value) => {
                            setWheelShowText(true)
                            localList.forEach((node)=>{
                              if(node.name.includes(wheelData[prizeNumber].option.split('...').join(''))){
                                setTimeout(()=>{
                                  setWheelShowText(false)
                                  setMustSpin(false);
                                  setTab(2)  
                                  setSelectedRestaurant(node)
                                },1000)
                              }
                            })
                        }}/>
                        <button
                        className="w-8 h-8  bg-white z-10 absolute font-bold rounded-full text-[11px]"
                        style={{
                          "position":"absolute",
                          "top":'50%',
                          "left": '50%',
                          "transform": "translate(-50%, -50%)",
                          "boxShadow" :'3px 3px rgba(0,0,0,0.8)'
                        }}
                        onClick={handleSpinClick}></button>
                      </>
                    }
                    </div>
                   
                    {wheelShowText && 
                    <div className="w-100 font-bold"
                        style={{
                          "position":"absolute",
                          "top":'50%',
                          "left": '50%',
                          "transform": "translate(-50%, -50%)",
                          "background":  "#181818",
                          "borderTop": '2px white solid',
                          "borderBottom": '2px white solid',
                          "zIndex":30,
                          "color": "white"
                        }}>
                        <div className="text-center p-8">
                            <div className="text-[16px] mb-4"></div>
                            <div className="text-[20px]">"{selectedRestaurant.name}"</div>
                        </div>
                    </div> 
                        }
                  </div>
                    }[tab]}
                </div>
              </div>
          </div>
      </div>
    </div> 
  )
}



export default React.memo(MyComponent)
